import React, { Component } from 'react'
import { Route } from "react-router-dom";
import { ViewTestEight } from '../components/Test/ViewTestEight';
import { ViewTestSeven } from '../components/Test/ViewTestSeven';
import { ViewTestOne } from '../components/Test/ViewTestOne';
import { ViewTestTwo } from '../components/Test/ViewTestTwo';
import { ViewTestThree } from '../components/Test/ViewTestThree';
import { ViewTestFour } from '../components/Test/ViewTestFour';
import { ViewTestFive } from '../components/Test/ViewTestFive';
import { ViewTestSix } from '../components/Test/ViewTestSix';
export class View extends Component {
  render() {
    return (
      <div>
         <Route
              path={process.env.PUBLIC_URL + "/ViewTestEight"}
              exact={true}
              component={ViewTestEight}
            />
             <Route
              path={process.env.PUBLIC_URL + "/ViewTestSeven"}
              exact={true}
              component={ViewTestSeven}
            />
             <Route
              path={process.env.PUBLIC_URL + "/ViewTestOne"}
              exact={true}
              component={ViewTestOne}
            />
             <Route
              path={process.env.PUBLIC_URL + "/ViewTestTwo"}
              exact={true}
              component={ViewTestTwo}
            />
             <Route
              path={process.env.PUBLIC_URL + "/ViewTestThree"}
              exact={true}
              component={ViewTestThree}
            />
            <Route
              path={process.env.PUBLIC_URL + "/viewTestFour"}
              exact={true}
              component={ViewTestFour}
            />
              <Route
              path={process.env.PUBLIC_URL + "/ViewTestFive"}
              exact={true}
              component={ViewTestFive}
            />
             <Route
              path={process.env.PUBLIC_URL + "/ViewTestSix"}
              exact={true}
              component={ViewTestSix}
            />
      </div>
    )
  }
}

export default View
