import React, { Component } from "react";
import { Route } from "react-router-dom";
import ParticleSize from "../components/Test/ParticleSize";
import FlakinessIndex from "../components/Test/FlakinessIndex";
import ElongationIndex from "../components/Test/Testtwo";
import Aggregate from "../components/Test/Testone";
import Waterabs from "../components/Test/Waterabs";
import SurfaceMoisture from "../components/Test/Testthree";
import Compaction from "../components/Test/Compaction";
import CrushingValue from "../components/Test/CrushingValue";
import BulkDensity from "../components/Test/BulkDensity";
import { SlumpCone } from "../components/RF - Form/RF-02/RF2";
import { ConcreteMoulds } from "../components/RF - Form/RF - 03/RF3";
import { Ambient } from "../components/RF - Form/RF - 04/RF4";
import { Audits } from "../components/RF - Form/RF - 01/RF1";
export class Add extends Component {
  render() {
    return (
      <div>
        <Route
          path={process.env.PUBLIC_URL + "/ParticleSize"}
          exact={true}
          component={ParticleSize}
        />
        <Route
          path={process.env.PUBLIC_URL + "/FlakinessIndex"}
          exact={true}
          component={FlakinessIndex}
        />
        <Route
          path={process.env.PUBLIC_URL + "/Testtwo"}
          exact={true}
          component={ElongationIndex}
        />
        <Route
          path={process.env.PUBLIC_URL + "/Testone"}
          exact={true}
          component={Aggregate}
        />
        <Route
          path={process.env.PUBLIC_URL + "/Waterabs"}
          exact={true}
          component={Waterabs}
        />
        <Route
          path={process.env.PUBLIC_URL + "/Testthree"}
          exact={true}
          component={SurfaceMoisture}
        />
        <Route
          path={process.env.PUBLIC_URL + "/Compaction"}
          exact={true}
          component={Compaction}
        />
        <Route
          path={process.env.PUBLIC_URL + "/CrushingValue"}
          exact={true}
          component={CrushingValue}
        />
        <Route
          path={process.env.PUBLIC_URL + "/BulkDensity"}
          exact={true}
          component={BulkDensity}
        />
        <Route
          path={process.env.PUBLIC_URL + "/SlumpCone"}
          exact={true}
          component={SlumpCone}
        />
        <Route
          path={process.env.PUBLIC_URL + "/ConcreteMoulds"}
          exact={true}
          component={ConcreteMoulds}
        />
        <Route
          path={process.env.PUBLIC_URL + "/Ambient"}
          exact={true}
          component={Ambient}
        />
        <Route
          path={process.env.PUBLIC_URL + "/Audits"}
          exact={true}
          component={Audits}
        />
      </div>
    );
  }
}

export default Add;
